export default "body{\n    margin: 0;\n    padding: 0;\n    display: flex;\n    justify-content:center;\n     align-items: center; \n    min-height: 100vh;\n    background: #212121;\n    font-family: sans-serif;\n  }\n  .box{\n    /* width: 1200px; */\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));\n    grid-gap: 15px;\n    margin: 0 auto;\n  }\n  .card{\n    position: relative;\n    width: 300px;\n    height: 350px;\n    background: #fff;\n    margin: 0 auto;\n    border-radius: 4px;\n    box-shadow:0 2px 10px rgba(0,0,0,.2);\n  }\n  .card:before,\n  .card:after\n  {\n    content:\"\";\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    border-radius: 4px;\n    background: #fff;\n    transition: 0.5s;\n    z-index:-1;\n  }\n  .card:hover:before{\n  transform: rotate(20deg);\n  box-shadow: 0 2px 20px rgba(0,0,0,.2);\n  }\n  .card:hover:after{\n  transform: rotate(10deg);\n  box-shadow: 0 2px 20px rgba(0,0,0,.2);\n  }\n  .card .imgBx{\n  position: absolute;\n  top: 10px;\n  left: 10px;\n  bottom: 10px;\n  right: 10px;\n  background: #222;\n  transition: 0.5s;\n  z-index: 1;\n  }\n  .card:hover .imgBx\n  {\n    bottom: 80px;\n  }\n  .card .imgBx img{\n      position: relative;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n      -o-object-fit: cover;\n         object-fit: cover;\n  }\n  .card .details{\n      position: absolute;\n      left: 10px;\n      right: 10px;\n      bottom: 10px;\n      height: 60px;\n      text-align: center;\n  }\n  /*   .card .details h2{\n   margin: 0;\n   padding: 0;\n   font-weight: 600;\n   font-size: 20px;\n   color: #777;\n   text-transform: uppercase;\n  }  */\n  .card .details h2 span{\n  font-weight: 500;\n  font-size: 16px;\n  color: #f38695;\n  display: block;\n  margin-top: 5px;\n   } \n/*# sourceMappingURL=data:application/json;base64, */"